import React, { useState } from 'react'

export default function TextForm(props) {

    const handleupclick = () => {
        console.log("Uppercase was Cliclked");
        let newText = Text.toUpperCase();
        setText(newText);
    }

    const handleonchange = (event) => {
        // console.log("On change")
        setText(event.target.value)
    }

    const handleLoClick = () => {
        console.log("Lowercase was clicked");
        let newText = Text.toLowerCase();
        setText(newText);
    }
    const handleLochange = (event) => {
        // console.log("On change")
        setText(event.target.value)
    }

    const handleChange = (e) => {
        setText(e.target.value);
    };

    const [Text, setText] = useState("");
    return (
        <>
            <div className="container">
                <h1>{props.heading}</h1>
                <div className="my-3">
                    <textarea className="form-control" value={Text} onChange={handleonchange} id="textBox" rows="7"></textarea>
                    <button className="btn btn-primary mx-1 my-2" onClick={handleupclick}>Convert to Uppercase</button>
                    <button className="btn btn-primary mx-1" onClick={handleLoClick}>Convert to Lowercase</button>

                </div>
            </div>
            <div className="container my-3">
                <h2>Your Text Summury</h2>
                <p>{Text.split(" ").length} Words and {Text.length} Characters</p>
                <p>{0.008 * Text.split(" ").length} Minutes read</p>
                <h2>Preview</h2>
                <p>{Text}</p>
            </div>
        </>
    )

}
